body {
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, sans-serif;
    margin:0px;
    padding:0px;
}

a {
  color: slategrey;
  background: transparent;
  word-wrap: break-word;
  word-break: break-all;
  text-decoration: none;
}

a:active, a:hover {
    color: tomato;
    outline: 1;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 15px;
  margin-bottom: 15px;
  line-height: 1.1;
}

nav ul {
    position: fixed;
    top: 0;
    font-size: 18px;
    margin: 0;
    padding: 0;
    list-style-type: none;
    width: 100%;
    background-color: #f1f1f1;
}

nav li {
    float: left;
    padding-left: 15%;
}

nav li a {
    display: block;
    text-align: center;
    padding: 14px 16px;
}


h1 {
  padding-bottom: 0.3em;
  font-size: 2.25em;
  line-height: 1.2;
  border-bottom: 1px solid #eee;
}

h2 {
  padding-bottom: 0.3em;
  font-size: 1.75em;
  line-height: 1.225;
  border-bottom: 1px solid #eee;
}

h3 {
  font-size: 1.5em;
  line-height: 1.43;
}

h4 {
  font-size: 1.25em;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 1em;
  color: #777;
}

.column {
  float: left;
}

.left {
  width: 25%;
  margin-right: 5%;
}

.right {
  width: 70%;
}

.current {
    color: teal;
}

.main_box {
    width: 70%;
    margin: 5% auto;
    padding: 2%;
    color: #333;
    font-family: "Helvetica Neue", Helvetica, "Segoe UI", Arial, sans-serif;
    font-size: 16px;
    line-height: 1.6;
    word-wrap: break-word;
}

.catalog {
    list-style-type: none;
}
